<template>
  <div class="header">
    <h1>大学生成绩管理系统</h1>
    <el-button type="success" @click="logout" round icon="el-icon-switch-button">退出登录</el-button>
  </div>
</template>

<script>
import axios from "axios";
import service from "@/request";
export default {
  name: "Header",
  methods: {
    logout() {
      axios.get(service.baseURL+"/logout", {
        headers: {
          Authorization: this.$store.state.token
        }
      }).then((resp) => {
        if (resp.data.code = '20000') {
          this.$store.commit('REMOVE_INFO')
          this.$router.push('/login')
          this.$message.success(resp.data.msg)
        } else {
          this.$message.error("注销失败")
        }
      })
    }
  }
}
</script>

<style scoped>

.header {
  height: 60px;
  line-height: 60px;
  text-align: center;
}

.el-button {
  float: right;
}

h1 {
  display: inline;
  color: #ff8000;
  font-size: 40px;
}
</style>